import Vue from 'vue'
import Router from 'vue-router'
import Home from './views/Home.vue'

Vue.use(Router)
export default new Router({
  routes: [
    {
      path: '/',
      name: 'home',
      component: Home,
      children: [{
        path: '/schedule-curriculum',
        name: 'Curriculum',
        component: () => import('./views/schedule/Curriculum.vue')
      }, {
        path: '/schedule-students',
        name: 'Students',
        component: () => import('./views/schedule/Students.vue')
      } ]
    },
    {
      path: '/about',
      name: 'about',
      component: () => import('./views/About.vue')
    }
  ]
})
